@use "global" as *;

::placeholder {
  color: $lightgreen;
}

.form__wrap:first-of-type {
  margin-top: 40px;
}

.form__wrap {
  padding-bottom: rem(24);
  border-bottom: 1px dashed $green;

  @include mq(md) {
    display: flex;
    // align-items: center;
    padding-bottom: rem(40);
  }
}

.form__wrap.form__wrap--textarea {
  @include mq(md) {
    align-items: flex-start;
  }
}



.form__wrap+.form__wrap {
  margin-top: rem(21);

  @include mq(md) {
    margin-top: rem(39);
  }
}



.form__label {
  // display: flex;
  // align-items: center;
  font-size: rem(16);
  font-weight: $bold;

  @include mq(md) {
    max-width: rem(220);
    width: 100%;
  }
}



.form__label span {
  margin-left: rem(8);
  padding: rem(2) rem(12);
  display: inline-block;
  font-size: rem(12);
  font-weight: $regular;
  color: #fff;
  background-color: #C94800;
  letter-spacing: 0.4em;
  text-indent: 0.4em;
}

.form__input,
.form__select,
.form__radio,
.form__checkbox,
.form__textarea {
  margin-top: rem(16);

  @include mq(md) {
    margin-top: 0;
    width: 100%;
  }
}

.form__checkbox {
  @include mq(md) {
    margin-top: rem(2);
  }
}


.form__input,
.form__textarea {
  flex-grow: 1;
}

.form__submit {
  margin-top: 3.75rem;
  text-align: center;
}

.form__input input {
  padding: rem(7) rem(16);
  width: 100%;
  font-size: rem(16);
  border: 1px solid $green;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  background-image: none;
}

.form__input input:focus {
  border: 1px solid #ccc;
  box-shadow: none;
  outline: none;
}



.form__select select {
  padding: rem(12) rem(8);
  width: 100%;
  font-size: rem(12);
  line-height: 1.2;
  border-radius: 0;
  border: 1px solid $green;
  background-image: url(./../images/common/arrow.svg);
  background-repeat: no-repeat;
  background-size: rem(12);
  background-position: right rem(6) top rem(15);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
}

/* IEで矢印を消す */
.form__select select::-ms-expand {
  display: none;
}

/* 選択時の枠線の色を変える */
.form__select select:focus {
  border: 1px solid #ccc;
  box-shadow: none;
  outline: none;
}

.form__checkbox label {
  display: block;
}

.form__checkbox label:not(:first-of-type) {
  margin-top: rem(3);

  @include mq(md) {
    margin-top: rem(4);
  }
}

.form__checkbox input {
  /* display: none; */
  display: block;
  height: rem(14);
  opacity: 0;
  position: absolute;
  width: rem(14);
  z-index: -1;
  margin: 0;
  margin-top: rem(5);
}

.form__checkbox input+span {
  padding: 0 0 0 rem(22);
  position: relative;
  display: block;
  font-size: rem(16);
  // line-height: 1;
  cursor: pointer;
}



.form__checkbox input+span::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid $green;
  display: block;
  width: rem(14);
  height: rem(14);

}

.form__checkbox input+span::after {
  content: "";
  margin-top: -0.125rem;
  position: absolute;
  top: 50%;
  left: rem(3);
  transform: translateY(-35%) rotate(-30deg);
  display: block;
  width: rem(9);
  height: rem(5);
  border-bottom: 2px solid #C94800;
  border-left: 2px solid #C94800;
  transition: 0.3s;
  opacity: 0;
}


.form__checkbox input:checked+span::after {
  opacity: 1;
}


.form__textarea textarea {
  padding: rem(8) rem(16);
  width: 100%;
  height: rem(200);
  font-size: rem(16);
  border-radius: 0;
  border: 1px solid $green;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  background-image: none;
}

.form__textarea textarea:focus {
  border: 1px solid #ccc;
  box-shadow: none;
  outline: none;
}

.form__privacyCheck {
  margin-top: rem(40);
  // padding: 0 8.5333% 0 16.2666%;
  font-size: rem(14);
  padding-left: rem(31);
  padding-right: rem(22);

  @include mq(md) {
    margin-top: rem(80);
    padding: 0;
  }
}

.form__privacyCheck-wrapper {
  padding-left: rem(30);
  margin-inline: auto;
  max-inline-size: max-content;
  position: relative;

  @include mq(md) {
    padding-left: rem(38);
  }
}

.form__privacyCheck-wrapper input {
  display: block;
  width: rem(22);
  height: rem(22);
  position: absolute;
  // left: rem(-3);//ワードプレス調整
  left: rem(-34); //ワードプレス調整
  top: rem(-2);

  @include mq(md) {
    left: rem(-42); //ワードプレス調整
  }
}


.form__privacyCheck-wrapper input+span::before {
  content: "";
  position: absolute;
  top: rem(11);
  // left: 0;//ワードプレス調整
  left: rem(-30); //ワードプレス調整
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid $green;
  display: block;
  width: rem(22);
  height: rem(22);

  @include mq(md) {
    left: rem(-38); //ワードプレス調整
  }
}

.form__privacyCheck-wrapper input+span::after {
  content: "";
  position: absolute;
  top: rem(9);
  // left: rem(5);//ワードプレス調整
  left: rem(-25); //ワードプレス調整
  transform: translateY(-50%) rotate(-45deg);
  display: block;
  width: rem(12);
  height: rem(8);
  border-bottom: 2px solid #C94800;
  border-left: 2px solid #C94800;
  transition: 0.3s;
  opacity: 0;

  @include mq(md) {
    left: rem(-32); //ワードプレス調整
  }
}

.form__privacyCheck-wrapper input:checked+span::after {
  opacity: 1;
}

