.form__button {
  margin-top: rem(24);
  text-align: center;

  @include mq(md) {
    margin-top: rem(40);
  }
}

.form__textarea textarea {
  display: block;
}

.form__error {
  display: none;
  font-size: rem(14);
  color: #C94800;
  text-align: center;
}

.form__input.error input,
.form__textarea.error textarea,
.form__checkbox.error input {
  border: 1px solid #C94800;
  background-color: rgb(201, 72, 0, 20%);
  /* ピンクの塗りつぶし */
}



.form__input.error input::placeholder {
  color: #fff;
}

.form__checkbox.error input+span::before,
.form__privacyCheck-wrapper.error input+span::before {
  border: 1px solid #C94800;
  background-color: rgb(201, 72, 0, 20%);
  /* ピンクの塗りつぶし */
}


//ワードプレス調整

.wpcf7-spinner {
  display: none !important;
}

.wpcf7-list-item {
  display: block !important;
  margin: 0 !important;
}